











import { Component, Prop, Vue } from 'vue-property-decorator';
import axios from 'axios';
import TableView from '@/components/TableView.vue';

var config = {
    headers: {'Access-Control-Allow-Origin': '*'}
};


@Component({
  components: {
    TableView,
  },
})
export default class HelloWorld extends Vue {
  private action: string[] = ["persons","debts"];
  private obj: string[] = new Array(this.action.length);
  private isLoad: boolean = false;
  created() {
    for (let i = 0; i < 1; i++) {
        this.req(i);
        console.log("created" + i);
    }
    this.isLoad = true;
  }

  req(ind: Number) {
      axios.get(`http://localhost:8080/api/${this.action[ind]}`, config)
      .then(res => {
          this.obj[ind] = res.data; console.log("act"); 
        }
      )
     .catch(err => {
       console.log("act")
      //console.log(err);
     });
  }
}
